import { loadJSON }     from '../functions/load-json.function'
import { loadScript }   from '../functions/load-script.function'
import { CLIENTS }      from '../constants/clients.constant'
import { itemInArray }  from '../functions/item-in-array.function'

/** @namespace f1x2 */
window.f1x2 = window.f1x2 || Object.create(null)

/**
 * @file Defines the {@link ClientWrapperInit} class
 * @author Callan Heard <callan.heard@1x2gaming.com>
 * @version 1.6.1
 * Changelog
 * Joe Thompson [09-10-2020] - GFI-97 new: getAccountInfo jurisidiction override
 * Joe Thompson [13-10-2021] - GFI-100 new: CLIENT_WRAPPER_INIT showInGameErrors getter
 * Joe Thompson [02-02-2021] - GFI-100 new: ClientWrapperReceiveMessage sync-balance
 * Joe Thompson [08-02-2021] - GFI-100 bug: CLIENT_WRAPPER_INIT.syncBalance true only if handler defined and supports sync-balance receive message
 */

/** Used to load client wrapper handler scripts and initialise communication if required for current client */
export class ClientWrapperInit {

    /**
     * Base URL path after CDN for client wrapper resources
     * @type {string}
     * @static
     */
    static baseURL = `infrastr_generic-framework/client-wrapper.`

    /**
     * Queue of messages sent before asynchronous loading/initialisation completes
     * @type {Array.<Array.<ClientWrapperSendMessage | any>>}
     * @private
     */
    _queue = []

    /** @private */ _use = true
    /** @private */ _callbacks
    /** @private */ _ready
    /** @private */ _handler

    /**
     * Determine whether usage required and act accordingly
     * @param {ClientWrapperLaunchParams} _launchParams_ Launch parameter values used by handler classes
     * @param {ClientWrapperCallbacks} _callbacks_ Game callbacks for handling received message type
     */
    constructor(_launchParams_, _callbacks_, _legacy_, _gameInfo_) {

        let { pathCDN, siteID } = _launchParams_                                                        //PathCDN, siteID launch parameter value
        pathCDN                 = `${pathCDN}${pathCDN.slice(-1) === '/' ? '' : '/'}`                   //Initialise pathCDN with trailing forward-slash
        let clientName          = String(_launchParams_.clientName).toUpperCase()                       //Default clientName to uppercase
        clientName              = CLIENTS[clientName]  || clientName                                    //Map client name if needed
        let listURL             = `${pathCDN}${ClientWrapperInit.baseURL}list.json`                     //Path to wrapper clients list
        let legacyURL           = `${pathCDN}${ClientWrapperInit.baseURL}legacy.json`                   //Path to legacy wrapper clients list
        this._callbacks         = _callbacks_                                                           //Store callbacks
        let legacy              = _legacy_ ? loadJSON(legacyURL) : Promise.resolve(Object.create(null)) //Promise for loading legacy list
        let clientHandlerName   = clientName                                                            //Client handler name

        _launchParams_.pathCDN = pathCDN;                                                               //Update launch params passed through to the game (IDS not loading client wrapper)

        //Initialise ready
        this._ready = new Promise(resolve_ => {

            //Load wrapper lists
            Promise.all([

                loadJSON(listURL),  //Load list of wrapper clients
                legacy              //Load list of legacy wrapper clients
                
            ]).then(list_ => {

                //If wrapper handling being used
                if (list_[0][clientName] || list_[1][clientName]) {
                    // if type is not simply true (1) and is an object
                    if ( !list_[1][clientName] && isNaN(list_[0][clientName]) ) {
                        // convert negative sideID
                        siteID = !isNaN(siteID) ? Math.abs(siteID) : siteID[0] === '-' ? siteID.replace('-', '') : siteID
                        // check if in array
                        if ( itemInArray( list_[0][clientName].sites, siteID ) ) {
                            clientHandlerName += `_${siteID}`   // site ids match, append _siteID to handler name
                        } 
                        //Else wrapper handling not required
                        else {
                            this._use = false   //Set flag to prevent further messages being stored
                            resolve_()          //Resolve ready Promise
                            return
                        }
                    }
                    
                    //Load wrapper handling script
                    loadScript(`${pathCDN}${ClientWrapperInit.baseURL}main.min.js`).then(() => {
                        
                        //Initialise handler
                        this._handler = new window.f1x2.CLIENT_WRAPPER_HANDLERS[clientHandlerName](_launchParams_, _callbacks_)

                        //Wait for handler ready
                        this._handler.ready.then(() => {
                            
                            //Send any queued messages
                            this._queue.map(() => {
                            
                                let m = this._queue.shift() //Queued message to send
                                this.send(m[0], m[1])       //Send message
                            
                            })

                            resolve_() //Resolve ready Promise
                            
                        })

                    })

                }
                //Else wrapper handling not required
                else {
                    
                    this._use = false   //Set flag to prevent further messages being stored
                    resolve_()          //Resolve ready Promise

                }

            })

        })

    }

    /**
     * Flag for whether wrapper handling is being used and messages should be queued
     * @type {boolean}
     * @private
     */
    get use() { return this._use && (!this._handler || this._handler.use) }

    /**
     * Game callbacks for handling received message type
     * @type {ClientWrapperCallbacks}
     */
    get callbacks() { return this._callbacks }

    /**
     * Delay loginComplete for any required asynchronous initialisation
     * @type {Promise}
     */
    get ready() { return this._ready }

    /**
     * Update {@link ClientWrapperInit.user}
     * @type {ClientWrapperHandler.user}
     */
    set user(v_) { this._handler && (this._handler.user = v_) }

    /**
     * Whether lobby URL navigation should be handled by game
     * @type {boolean}
     */
    get doLobbyURLNavigate() { return !this._handler || this._handler.doLobbyURLNavigate }

    /**
     * Handler for current client
     * @type {ClientWrapperHandler}
     */
    get handler() { return this._handler }

    /**
     * Whether to show in game errors
     * @type {boolean}
     */
    get showInGameErrors() { return !this._handler || this._handler.showInGameErrors }

    /**
     * Whether to sync balance by requesting from server before placing bet
     * @type {boolean}
     */
    get syncBalance() { return this._handler && this._handler.syncBalance }

    /** 
     * Whether to hide win/loss.
     * @type {boolean}
     */
    get hideSessionBar()  { return (this._handler && this._handler.hideSessionBar) || false }

    /**
     * Attempt to send a message to handler
     * @param {ClientWrapperSendMessage} m_ Name of message to send
     * @param {any} [arg_] Arguments to send alongside message
     */
    send(m_, arg_) {

        //If handler initialised and no stored messages before this one
        if (this._handler && this._queue.length === 0) {
            this._handler.send(m_, arg_) //Send message
        }
        //Else if should still be queued
        else if (this._use) {
            this._queue.push([ m_, arg_ ]) //Queue message
        }
            
    }

}