/**
 * @file Defines the {@link CLIENTS} constant
 * @author Callan Heard <callan.heard@1x2gaming.com>
 */

/**
 * Map of client name values to use for the same config name
 * @type {Object.<string, string[]>}
 */
const CONFIG = {
    'NYX': [ 'NYX_DEV' ],
    'NYX_GIB': [ 'NYX_GIB_DEV' ],
    'ISOFTBET': [ 'ISOFTBET_DEV' ],
    'IFORIUM': [ 'IFORIUM_DEV' ],    
    'GAMEIOM_GIB': [ 'GAMEIOM_DEV' ],
    'GAN': [ 'GAN_DEV' ],
    'BWIN': [ 'BWIN_DEV' ],
    'PARIPLAY': [ 'PARIPLAY_STAGING' ],
    '3PI': [ '3PI_DEV' ],
    'SBTECH': [ 'SBTECH_STAGING', 'SBTECH_DEV' ],
    'PLAYTECH': [ 'PLAYTECH_DEV' ],
}

/**
 * Inverted/associative format config for lookup
 * @type {Object.<string, string>}
 */
const CLIENTS = Object.create(null)

//Loop through config
for (let c in CONFIG) {
    CONFIG[c].map(v_ => CLIENTS[v_] = c) //Map client names to config value to use
}

export { CLIENTS }