/**
 * @file Defines the {@link loadFile} function
 * @author Callan Heard <callan.heard@1x2gaming.com>
 */

/**
 * Load a file from a URL using XMLHttpRequest
 * @param {string} url_ URL for the file
 * @param {string} [query_] Query string for request
 * @param {boolean} [post_] Use POST method instead of GET
 * @returns {Promise} Promise resolving on request complete with response
 */
export function loadFile(url_, query_, post_) {

    let url     = url_.slice(-1) === '?' ? url_.slice(0, -1) : url_                     //Handle ? on given url
    let query   = query_ ? (query_.slice(0, 1) === '?' ? query_ : `?${query_}`) : ''    //Handle ? on given query string
    let path    = `${url}${query}`                                                      //Initialise path from url/query string

    //Initialise and return response Promise
    return new Promise((resolve_, reject_) => {

        let request = new XMLHttpRequest() //XMLHttpRequest for request

        //Handle ready state change
        request.onreadystatechange = function() {

            //If request complete and successful
            if (this.readyState == 4 && this.status == 200) {
                resolve_(this.responseText) //Resolve response Promise
            }

            //If request complete and fails
            if (this.readyState == 4 && (this.status == 404 || this.status == 0)) {
                reject_(this.status) //Reject response Promise
            }

        }

        request.open(post_ ? 'POST': 'GET', path, true) //Open request
        request.send()                                  //Send request

    })

}