import { loadFile } from './load-file.function'

/**
 * @file Defines the {@link loadJSON} function
 * @author Callan Heard <callan.heard@1x2gaming.com>
 */

/**
 * Load a JSON file from a URL using XMLHttpRequest
 * @param {string} url_ URL for the file
 * @param {string} [query_] Query string for request
 * @returns {Promise} Promise resolving on request complete with response parsed to an object
 */
export function loadJSON(url_, query_) {
    return loadFile(url_, query_).then(r_ => JSON.parse(r_)).catch(e_ => JSON.parse(e_)) //Load file and parse response
}