/**
 * @file Defines the {@link loadScript} function
 * @author Callan Heard <callan.heard@1x2gaming.com>
 */

/**
 * Load and execute external JavaScript file by adding a script tag to the document
 * @param {string} url_ URL for the script
 * @returns {Promise} Promise resolving on script loaded
 */
export function loadScript(url_) {

    //Initialise and return response Promise
    return new Promise(resolve_ => {

        let script      = document.createElement('script')
        script.type     = 'text/javascript'
        script.async    = true
        script.onload   = resolve_
        script.src      = url_

        document.body.appendChild(script)

    })

}