/**
 * @file Defines the {@link itemInArray} function
 * @author Adam O'Brien <adam.obrien@1x2network.com>
 */

/**
 * Check if an item exist in an array
 * @param {array} array_ Array for the list
 * @param {number} item_ item string for request
 * @returns {boolean} true if the array contains the item
 */
export function itemInArray( array_, item_ ) {

    for (let i = 0; i < array_.length; i++) {

        if ( +array_[i] === +item_ ) {
            return true;
        }

    }
    return false;

}